import { createLoggers, ContentSerdes, Helpers, ProtocolHelpers, Servient, } from '@node-wot/core';
import Ajv from 'ajv';
import * as util from 'util';
import { JsonPlaceholderReplacer } from 'json-placeholder-replacer';
import { HttpsClientFactory } from '@node-wot/binding-http';
import { BluetoothClientFactory } from './bindings/binding-bluetooth/Bluetooth';
import ConcreteBluetoothAdapter from 'BluetoothAdapter';
import { HidClientFactory } from './bindings/binding-hid/Hid';
import ConcreteHidAdapter from 'HidAdapter';
export { EddystoneHelpers } from './bindings/binding-bluetooth/EddystoneHelpers';
let servient;
let wot;
let wotHelpers;
const { debug } = createLoggers('wot');
const ajv = new Ajv({ strict: false });
export const getServient = function (bluetoothAdapter, hidAdapter) {
    const httpConfig = {
        port: 8083,
        allowSelfSigned: true, // client configuration
    };
    if (!servient) {
        servient = new Servient();
        servient.addClientFactory(new BluetoothClientFactory(bluetoothAdapter));
        servient.addClientFactory(new HttpsClientFactory(httpConfig));
        servient.addClientFactory(new HidClientFactory(hidAdapter));
        wotHelpers = new Helpers(servient);
    }
    return servient;
};
const getWot = async function (bluetoothAdapter = new ConcreteBluetoothAdapter(), hidAdapter = new ConcreteHidAdapter()) {
    if (!wot) {
        wot = await getServient(bluetoothAdapter, hidAdapter).start();
    }
    return wot;
};
export const resetServient = async function () {
    if (servient) {
        // destroy exposed things
        const things = servient.getThings();
        Object.entries(things).forEach(async ([id]) => {
            const thing = servient.getThing(id);
            if (thing) {
                await thing.destroy();
            }
        });
        await servient.shutdown();
    }
};
export const createThing = async function (td, id) {
    if (id) {
        const map = {
            MacOrWebBluetoothId: id,
            HIDPATH: id,
        };
        // deep copy td, because the original td is imported as module and can't be modified.
        td = structuredClone(td);
        td = fillPlaceholder(td, map);
    }
    const wotServient = await getWot();
    const exposedThing = await wotServient.produce(td);
    const consumedThing = await wotServient.consume(exposedThing.getThingDescription());
    return consumedThing;
};
export const createThingWithHandlers = async function (td, id, addHandlers) {
    if (id) {
        const map = {
            MacOrWebBluetoothId: id,
            HIDPATH: id,
        };
        // deep copy td, because the original td is imported as module and can't be modified.
        td = structuredClone(td);
        td = fillPlaceholder(td, map);
    }
    const wotServient = await getWot();
    const exposedThing = await wotServient.produce(td);
    addHandlers(exposedThing);
    const consumedThing = ownConsume(exposedThing);
    return consumedThing;
};
const fillPlaceholder = function (data, map = {}) {
    const placeHolderReplacer = new JsonPlaceholderReplacer();
    placeHolderReplacer.addVariableMap(map);
    return placeHolderReplacer.replace(data);
};
const ownConsume = function (exposedThing) {
    const consumedThing = {};
    consumedThing.id = exposedThing.getThingDescription().id;
    consumedThing.name = exposedThing.getThingDescription().title;
    consumedThing.subscribeEvent = async function (name, listener, errorListener, options) {
        const td = exposedThing.getThingDescription();
        if (exposedThing.events && exposedThing.events[name]) {
            const eventElement = exposedThing.events[name];
            const contentListener = (data) => {
                // construct IneractionOutput from data
                const output = new InteractionOutput(data);
                listener(output);
            };
            exposedThing.__eventListeners.register(eventElement, 0, contentListener);
            return {
                active: true,
                stop: async (options) => {
                    exposedThing.__eventListeners.unregister(eventElement, 0, contentListener);
                },
            };
        }
        else {
            throw new Error(`Event '${name}' not found`);
        }
    };
    return consumedThing;
};
class InteractionOutput {
    content;
    parsedValue;
    buffer;
    _stream;
    dataUsed;
    form;
    schema;
    get data() {
        if (this._stream) {
            return this._stream;
        }
        if (this.dataUsed) {
            throw new Error("Can't read the stream once it has been already used");
        }
        // Once the stream is created data might be pulled unpredictably
        // therefore we assume that it is going to be used to be safe.
        this.dataUsed = true;
        return (this._stream = ProtocolHelpers.toWoTStream(this.content.body));
    }
    constructor(content, form, schema) {
        this.content = content;
        this.form = form;
        this.schema = schema;
        this.dataUsed = false;
    }
    async arrayBuffer() {
        if (this.buffer) {
            return this.buffer;
        }
        if (this.dataUsed) {
            throw new Error("Can't read the stream once it has been already used");
        }
        const data = await this.content.toBuffer();
        this.dataUsed = true;
        this.buffer = data;
        return data;
    }
    async value() {
        // the value has been already read?
        if (this.parsedValue) {
            return this.parsedValue;
        }
        if (this.dataUsed) {
            throw new Error("Can't read the stream once it has been already used");
        }
        // read fully the stream
        const data = await this.content.toBuffer();
        this.dataUsed = true;
        this.buffer = data;
        // call the contentToValue
        // TODO: should be fixed contentToValue MUST define schema as nullable
        const value = ContentSerdes.get().contentToValue({ type: this.content.type, body: data }, this.schema ?? {});
        // any data (schema)?
        if (this.schema) {
            // validate the schema
            const validate = ajv.compile(this.schema);
            if (!validate(value)) {
                debug(`schema = ${util.inspect(this.schema, { depth: 10, colors: true })}`);
                debug(`value: ${value}`);
                debug(`Errror: ${validate.errors}`);
                throw new DataSchemaError('Invalid value according to DataSchema', value);
            }
        }
        this.parsedValue = value;
        return this.parsedValue;
    }
}
class NotSupportedError extends Error {
    constructor(message) {
        super(message);
        // Set the prototype explicitly.
        // TS limitation see https://github.com/Microsoft/TypeScript-wiki/blob/master/Breaking-Changes.md#extending-built-ins-like-error-array-and-map-may-no-longer-work
        Object.setPrototypeOf(this, NotSupportedError.prototype);
    }
}
class DataSchemaError extends Error {
    value;
    constructor(message, value) {
        super(message);
        this.value = value;
        // Set the prototype explicitly.
        // TS limitation see https://github.com/Microsoft/TypeScript-wiki/blob/master/Breaking-Changes.md#extending-built-ins-like-error-array-and-map-may-no-longer-work
        Object.setPrototypeOf(this, NotSupportedError.prototype);
    }
}
